@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Recursive:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  margin:0;
  padding:0;
}

.App p{
  text-align: start;
  padding-left: 40px;
  font-size: 1.2rem;
  font-family: 'Recursive', sans-serif;
  
}
.App .footer{
  font-weight:lighter;
  font-size: 12px;
}

.Layout{
    width: 100hw;
    font-family: inherit;
    margin:60px 40px ;
    
    font-family: 'Recursive', sans-serif;
    
}
.Layout .content{
    width: 100%;
    height: 70vh;
    display: flex;
    text-align: start;
}

img{
    width:50%;
  }

.Layout .content .inside{
    width: 50%;
    font-weight: 300;
    padding-left: 40px;
    display: flex;
    flex-flow: column;
    justify-content: space-evenly;
    
}
.Layout .content .inside h1{
    font-size: 40px;
    padding-bottom: 30px;
}
.Layout .content .inside p{
    padding:0;
    font-weight: 300;
    width: 280px;
    font-size: 17px;
    padding-bottom: 50px;
}
.Layout button{
    width: 170px;
    padding:12px 5px;
    font:inherit;
    background-color: black;
    color:white;
    border: none;
    border-radius: 3px;
}
.Layout button:hover{
    /* color:black; */
    color: #ccc;

    cursor: pointer;
}

@media(max-width:650px){
    .Layout{
        margin: 20px;

    }
    .Layout .content{
        width:100%;
        height: 100%;
        display: block;
        text-align: center;
        margin-bottom: 30px;
    } 
    .Layout .content img{
        width: 60%;
        margin-left: 0;
        
    }
    .Layout .content .inside{
        padding-left: 10px;
        width: 80%;
        text-align: start;
    }
    .Layout .content .inside h1{
        font-size: 25px;
        padding-bottom: 30px;
        width: 100%;
    }
    .Layout .content .inside p{
        font-size: 15px;
    }
}
